<template>
  <div class="plan-list">
    <div class="title"></div>
    <div class="list">
      <auto-scroll-table
        :config="config"
        style="width:100%;height:820px;background:rgb(40,40,40);"
      />
    </div>
  </div>
</template>

<script>
  import { ref, watch } from 'vue'
  import AutoScrollTable from '../AutoScrollTable/index'

  export default {
    name: 'planList',
    components: { AutoScrollTable },
    props: {
      data: Array
    },
    setup(props) {
      const config = ref(null)

      watch(() => props.data, () => {
        const data = []
        const align = []
        const columnIndex = []
        for (let i = 0; i < props.data.length; i++) {
          data[i] = []
          align[i] = 'center'
          if (i % 2 === 0) {
            columnIndex[i] = `<div style="width:100%;height:100%;display:flex;align-items:center;justify-content:center;">
            <div style="width:15px;height:15px;background:rgb(72,122,72);border-radius:50%;border:1px solid #fff;"/>
          </div>`
          } else {
            columnIndex[i] = `<div style="width:100%;height:100%;display:flex;align-items:center;justify-content:center;">
            <div style="width:15px;height:15px;background:rgb(38,88,104);border-radius:50%;border:1px solid #fff;"/>
          </div>`
          }
          for (let j = 0; j < 5; j++) {
            let text = ''
            switch (j) {
              case 0:
                text = props.data[i].order
                break
              case 1:
                text = props.data[i].shop
                break
              case 2:
                text = props.data[i].rider
                break
              case 3:
                text = props.data[i].newShop
                break
              case 4:
                text = props.data[i].avgOrder
                break
              default:
            }
            if (j === 1 || j === 3) {
              data[i].push(`<div style="color:rgb(178,209,126)">${text}</div>`)
            } else {
              data[i].push(`<div>${text}</div>`)
            }
          }
        }
        config.value = {
          header: ['', '', '', '', ''], // 
          data, // 
          align, // 
          index: true, // 
          indexHeader: '', // 
          rowNum: 12, // 
          headerBGC: 'rgb(90, 90, 90)', // 
          oddRowBGC: 'rgb(40, 40, 40)', // 
          evenRowBGC: 'rgb(55, 55, 55)', // 
          headerHeight: 55, // 
          headerFontSize: 24, // 
          columnFontSize: 24, // 
          columnMarginTop: 20, // 
          columnMarginRight: 20, // 
          columnWidth: [80], // 
          columnIndex, // 
          autoPlay: true, // 
          columnStyle: [{ background: 'rgb(40, 40, 40)' }] // 
        }
      })
      return {
        config
      }
    }
  }
</script>

<style lang="scss" scoped>
  .plan-list {
    width: 100%;
    height: 100%;
    background: rgb(55, 55, 55);
    padding: 20px 40px;
    box-sizing: border-box;

    .title {
      font-size: 36px;
      margin-left: 20px;
    }

    .list {
      width: 100%;
      height: 880px;
      margin-top: 20px;
      padding: 30px 0;
      box-sizing: border-box;
      background: rgb(40, 40, 40);
    }
  }
</style>
